import { useContext } from "react";
import { NextMatchContext } from "../../providers/nextMatch";



export function StatusNextMatch() {
    const nextMatchContext = useContext(NextMatchContext)
    const { nextMatch } = nextMatchContext ? nextMatchContext : {}
    
    const valueAll = (num_next_cards) => {
        if (nextMatch.unitary_value) {
            return (num_next_cards * nextMatch.unitary_value).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })
        }
        return ''
    }

    return (
        <>
            <div className="col-12">
                <div className="bg-light rounded ">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-8">
                                <p className="fs-6 px-4 my-0 text-info fw-semibold ">Prxima Rodada:</p>
                            </div>
                            <div className="col">
                                <span className="fs-6 text-info fw-semibold">{nextMatch ? (nextMatch.next_round_prize ? nextMatch.next_round_prize.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' }) : nextMatch.next_round_prize) : ""}</span>
                            </div>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="row">
                            <div className="col-8">
                                <p className="fs-6 px-4 my-auto  text-info fw-semibold">Suas Cartelas:</p>
                            </div>
                            <div className="col">
                                <span className="fs-6 text-info fw-semibold"><span>{nextMatch ? (nextMatch.num_next_cards ? nextMatch.num_next_cards : nextMatch.num_next_cards) : ""}</span>/{nextMatch ? nextMatch.number_cards_allowed : ""}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-12 mt-1">
                <div className="bg-light rounded">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-8">
                                <p className="fs-6 px-4 my-0 text-info fw-semibold ">Valor Unitrio:</p>
                            </div>
                            <div className="col">
                                <span className="fs-6 text-info fw-semibold">{nextMatch ? nextMatch.unitary_value ? nextMatch.unitary_value.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' }) : nextMatch.unitary_value : ""}</span>
                            </div>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="row">
                            <div className="col-8">
                                <p className="fs-6 px-4 my-0 text-info fw-semibold ">Valor Total:</p>
                            </div>
                            <div className="col">
                                <span className="fs-6 text-info fw-semibold">{nextMatch ? nextMatch.num_next_cards ? valueAll(nextMatch.num_next_cards) : valueAll(0) : ""}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );

}