import { Modal } from 'react-bootstrap';
import 'keen-slider/keen-slider.min.css'
import { useKeenSlider } from 'keen-slider/react'
import { useState } from 'react';
import "./style.css"
import { CartelaVencedora } from '../Cartelas/CartelaVencedora';

export function ModalWin(props) {
  // console.log(props)
  const controls = document.querySelectorAll(".control-award-winners");
  let currentItem = 0;
  const items = document.querySelectorAll(".item-award-winners");
  const maxItems = items.length;
  let isLeft

  controls.forEach((control) => {
    control.addEventListener("click", (e) => {
      isLeft = e.target.classList.contains("arrow-left-award-winners");

      if (isLeft) {
        if (currentItem - 1 < 0) {
          currentItem = maxItems - 1;
        }else{
          currentItem -= 1;
        }
        
        
      } else {
        if (currentItem + 1 > maxItems) {
          currentItem = 0;
        }else{
          currentItem += 1;
        }
        
      }
      items.forEach((item) => item.classList.remove("current-item-award-winners"));

      items[currentItem].scrollIntoView({
        behavior: "smooth",
        inline: "center"
      });

      items[currentItem].classList.add("current-item-winners");
    });
  });
  return (
    <Modal
      {...props}
      size="sm"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >

      <Modal.Body className='canvas rounded ' >
        <button className="arrow-left-award-winners control-award-winners" aria-label="Previous image"></button>
        <button className="arrow-right-award-winners control-award-winners" aria-label="Next Image"></button>
        <div className='gallery-wrapper-award-winners'>
          <div className='gallery-award-winners'>
            {props ? props.winners.map((w, i) => (<ScrennWinner key={`scWinner${i}`} award={w.award} value={w.amount_received} name={w.user.name} card={w.card}></ScrennWinner>)) : undefined}

          </div>
        </div>
      </Modal.Body>

    </Modal>
  );
}


function ScrennWinner(props) {
  return (
    <>
      <div className={props.key == 1 ? "item-award-winners current-item-award-winners-winners" : "item-award-winners"}>
        <h5 className=' text-center'>TEMOS UM GANHADOR</h5>
        <h3 className=' text-center'>PARABNS</h3>
        <div className="container">
          <div className="row ">
            <div className="col">
              <h6 className='text-center bg-info p-2 text-light'>{props.award.name}</h6>
            </div>
            <div className="col">
              <h6 className='text-center bg-info p-2 text-light'>{props ? props.value.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' }) : "0"}</h6>
            </div>
          </div>
          <h5 className=' text-center p-2 bg-white'><span>{props.name}</span></h5>
          <div className=" m-1">
            <CartelaVencedora key={props.name} balls={props.card.balls} ></CartelaVencedora>
          </div>
        </div>
      </div>
    </>
  )
}