import React, { useState } from "react";
import "./style.css"

export function Cartela(props) {
    const [linha1, setLinha1] = useState(props.balls.slice( 0, 5))
    const [linha2, setLinha2] = useState(props.balls.slice(5, 10))
    const [linha3, setLinha3] = useState(props.balls.slice(10, 15))

    const formatLine = (nuns = []) => {
        let newNuns = []

        for (let i = 0 ; i < 9 ; i++){
            newNuns.push({number: "00", drawn: false})
        }
        for (let num of nuns){
            if (num.number >= 1 & num.number <= 10){
                newNuns[0] = num
            }else if (num.number >= 11 & num.number <= 20){
                newNuns[1] = num
            }else if (num.number >= 21 & num.number <= 30){
                newNuns[2] = num
            }else if (num.number >= 31 & num.number <= 40){
                newNuns[3] = num
            }else if (num.number >= 41 & num.number <= 50){
                newNuns[4] = num
            }else if (num.number >= 51 & num.number <= 60){
                newNuns[5] = num
            }else if (num.number >= 61 & num.number <= 70){
                newNuns[6] = num
            }else if (num.number >= 71 & num.number <= 80){
                newNuns[7] = num
            }else if (num.number >= 81 & num.number <= 90){
                newNuns[8] = num
            }
        }
        return newNuns
    }


    function setClassNum(num){
        if (num.number === "00"){
            return "bg-white text-white"
        }else if(num.drawn){
            return "bg-warning text-white"
        }else if (!num.drawn){
            return "bg-white"
        }else {
            return "bg-white"
        }
    }

    function classeCard(){
        let count = 0
        for (let ball of Array.from(props.balls)){
            if (!ball.drawn){
                count ++
            }
        }
        if (count === 1){
            return {classCard: "bg-green", classId: "flashes-green", classCardText: "text-black"}
        }else if(count === 2){
            return {classCard: "bg-rose", classCardText: "text-black"}
        }else if(count === 3){
            return {classCard: "bg-orange", classCardText: "text-black"}
        }else if(count === 4){
            return {classCard: "bg-blue", classCardText: "text-black"}
        }else if(count === 5){
            return {classCard: "bg-blackGreen", classCardText: "text-white"}
        }else{
            return {classCard: "bg-black", classCardText: "text-white"}
        }
    }

    return (
        <table id={props.id} className={"p-1 pt-0 m-1 " + classeCard().classCard}>
            <thead>
                <tr>
                    <th colSpan={9} className={"head-card " + classeCard().classCardText} >
                        comprada <span className={classeCard().classCard === "bg-green" ? "float-end "+classeCard().classId : "float-end"} >id: {String(props.id).padStart(6, "0")}</span>
                    </th>
                </tr>
                
            </thead>
            <tbody >
                <tr>
                    {formatLine(linha1).map(num => (
                        <td className={`fs-4 text-center ${setClassNum(num)}`}>{num.number}</td>
                    ))}
                </tr>
                <tr>
                    {formatLine(linha2).map(num => (
                        <td className={`fs-4 text-center ${setClassNum(num)}`}>{num.number}</td>
                    ))}
                </tr>
                <tr>
                    {formatLine(linha3).map(num => (
                        <td className={`fs-4 text-center ${setClassNum(num)}`}>{num.number}</td>
                    ))}
                </tr>
            </tbody>
        </table>
    )
}