import { useContext } from "react";
import { MatchContext } from "../../providers/match";


export function StatusMatchCurrent() {
    const matchContext = useContext(MatchContext)
    const { match } = matchContext ? matchContext : {}

    const dateTime = (match) => {
        if (match.datetime_initials) {
            const [date, time] = match.datetime_initials.split("T")
            return `${date} ${time}`
        }
    }
    return (
        <div className="col-12">
            <div className="mx-0">
                <div className=" bg-light rounded my-1  py-1  ">
                    <div className="row col-12 mx-0">
                        <div className="col"><h3 className="fs-6 px-4 my-0  text-uppercase  ">Rodada atual: <span>{match && match.id ? String(match.id).padStart(7, "0") : ''}</span></h3>
                        </div>
                        <div className="col">
                            <h3 className="fs-6 px-4 my-0  text-uppercase   "><span>{match ? dateTime(match) : ""}</span></h3>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )

}