import { Button, Modal, Form} from 'react-bootstrap';
import { useState } from 'react';
import React from 'react';
import RangeSlider from 'react-bootstrap-range-slider';


function MyVerticallyCenteredModal(props) {
  const [value, setValue] = useState(1)

  return (
    <Modal
      {...props}
      size="md"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Body className='p-4'>
        <button type="button" className="btn-close" aria-label="Close" onClick={props.onHide}></button>
        <h4>Selecione a quantidade desejada </h4>
        <Form>
          <RangeSlider
            className='w-100'
            value={props.numPay}
            onChange={e => props.setNumPay(parseInt(e.target.value))}
            min={props.min}
            max={props.max}
          />
        </Form>
        <div className="container">
          <div className="row">
            <div className="col">
            </div >
            <div className="col ">
              <Button className='rounded-pill bg-success text-light  px-4 ' onClick={_=>(props.setNumPay(props.max))}>TODAS</Button>
            </div>
            <div className="col">
              <Button className='rounded-pill bg-danger text-black mb-0 px-4 ' onClick={props.onHide}>OK</Button>
            </div>
            <div className="col">
            </div>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
}

export function Select(props) {

  const [modalShow, setModalShow] = useState(false);

  return (
    <>
      <button className="bg-primary border-0  fs-6 rounded-pill text-light " onClick={() => setModalShow(true)} type="button" ><span className="p-1">SELECIONAR FACIL</span></button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        min={props.min}
        max={props.max}
        numPay={props.numPay.numPay}
        setNumPay={props.numPay.setNumPay}
      />
    </>
  );







}



