import { useContext, useEffect, useState } from "react";
import "./style.css";
import { Ball } from "./ball";
import { Col } from "./col";
import { BingoContext } from "../../providers/bingo";
import { ModalWin } from "../Modal/ModalWin";
import { classesAwards } from "../award/classesAward";
import { ModalTop } from "../Modal/Modaltop";


export function RoundBalls() {
    const contextBingo = useContext(BingoContext)
    const bingo = contextBingo ? contextBingo.bingo : {}
    const setBingo = contextBingo ? contextBingo.setBingo : {}
    const colorsCard = ["bg-black", "bg-blackGreen", "bg-blue", "bg-orange", "bg-rose", "bg-green"]
    const [modalShow, setModalShow] = useState(false);
    const [modalTopShow, setModalTopShow] = useState(false);
    const [winners, setWinners] = useState([])


    let intervalBingo

    const funcIntervalBingo = _ => {
        const token = sessionStorage.getItem("token")

        fetch(`${window.urlAPI}/api/v1/bingo/`, {
            headers: {
                Authorization: `Token ${token}`
            }
        })
            .then(resp => {
                return resp.json()
            })
            .then(json => {

                setBingo({ ...json })
                let cards = JSON.parse(localStorage.getItem("cards"))


                if (localStorage.getItem("ball_drawn")) {
                    const nBallDrawn = JSON.parse(localStorage.getItem("ball_drawn"));
                    if (nBallDrawn !== json.ball_drawn) {
                        setModalShow(false)
                        localStorage.setItem("ball_drawn", json.ball_drawn)
                        const cardsTables = Array.from(document.querySelectorAll("#elemento2 > div table"));
                        cardsTables.forEach((ele) => {

                            let cont = 0

                            const cols = Array.from(ele.getElementsByTagName("td"));
                            cols.forEach((col) => {
                                if (col.textContent == json.ball_drawn) {
                                    col.classList.replace("bg-white", "bg-warning")
                                    col.classList.add("text-white")
                                }
                                if (col.classList.contains("bg-white") && col.textContent !== "00") {
                                    cont++
                                }
                            })
                            const cColor = currentClass(Array.from(ele.classList))

                            const idSpan = ele.querySelector("thead > tr > th > span")
                            const styleCard = classeCard(cont)
                            if (styleCard) {
                                ele.classList.replace(cColor, styleCard.classCard)
                                if (styleCard.classCardText === "text-black" && idSpan.parentNode.classList.contains("text-white")) {
                                    idSpan.parentNode.classList.replace("text-white", "text-black")
                                } else if (styleCard.classCardText == "text-white" && idSpan.parentNode.classList.contains("text-black")) {
                                    idSpan.parentNode.classList.replace("text-black", "text-white")
                                }
                            }
                            if (cont === 1) {
                                idSpan.classList.add("flashes-green")
                            }

                        })
                    }
                } else {
                    localStorage.setItem("ball_drawn", json.ball_drawn);
                }

                if (json.winners) {
                    if (localStorage.getItem("winners")) {
                        const winnersStorage = JSON.parse(localStorage.getItem("winners"))
                        let newWinners = []
                        json.winners.forEach((ele) => {
                            if ((winnersStorage.findIndex(_ => { return ((_.user.name === ele.user.name) && (_.award.name === ele.award.name)) })) === -1) {
                                newWinners.push(ele)
                            }
                        })
                        if (newWinners.length > 0) {
                            setWinners(newWinners)
                            setModalShow(true)
                            renderWinnersAwards(json.winners)
                            localStorage.setItem("winners", JSON.stringify(json.winners))
                        }


                    } else if (document.querySelector("div.owl-item:nth-child(1) > div:nth-child(1) > div:nth-child(3) > div:nth-child(1) > div:nth-child(1) > h5:nth-child(1)").textContent === "Linha 1") {
                        localStorage.setItem("winners", JSON.stringify(json.winners))
                        setWinners(json.winners)
                        setModalShow(true)
                        renderWinnersAwards(json.winners)
                    } else {
                        localStorage.setItem("winners", JSON.stringify(json.winners))
                        setWinners(json.winners)
                    }
                }

                if (json.finalized) {
                    setModalTopShow(true)
                    let count = localStorage.getItem("countNextMatch")
                    if (count) {
                        if (count == 3){
                            window.location.reload();                            
                        }
                        else{
                            localStorage.setItem("countNextMatch", JSON.stringify(parseInt(count)+1))
                        }
                    }else{
                        localStorage.setItem("countNextMatch", JSON.stringify(1))
                    }
                }
            })
            .catch(err => {
                console.log(`Erro aconteceu: ${err}`)
            });
    }

    function renderWinnersAwards(winners) {
        winners.forEach((w) => {
            console.log(w.user.name)
            let clsStyles
            let galleryElement
            let elemDiv
            let elem
            let text

            function createElem(nameAward) {
                clsStyles = classesAwards[nameAward]
                galleryElement = document.querySelector(`.${clsStyles.gallery}`)
                if (galleryElement.childNodes[0].textContent == nameAward) {
                    galleryElement.textContent = ''
                    elemDiv = document.createElement("div")
                    elemDiv.classList.value = `${clsStyles.item} ${clsStyles.currentItem}`
                    elem = document.createElement("h5")
                    elem.classList.value = "fontes2 fs-4"
                    text = document.createTextNode(w.user.name)
                    console.log(elem)
                    elem.appendChild(text)
                    elemDiv.appendChild(elem)
                    galleryElement.appendChild(elemDiv)
                } 
                else {
                    elemDiv = document.createElement("div")
                    elemDiv.classList.add(`${clsStyles.item}`)
                    elem = document.createElement("h5")
                    elem.classList.value = "fontes2 fs-4"
                    text = document.createTextNode(w.user.name)
                    elem.appendChild(text)
                    elemDiv.appendChild(elem)
                    galleryElement.appendChild(elemDiv)
                }
            }

            if (w.award.name === "linha 1") {
                createElem("linha 1")
            }
            if (w.award.name === "linha 2") {
                createElem("linha 2")
            } 
            if (w.award.name === "Bingo") {
                createElem("Bingo")
            } 
            if (w.award.name === "Bingo 2") {
                createElem("Bingo 2")
            } 
            if (w.award.name === "Bingo 3") {
                createElem("Bingo 3")
            }
        })
    }

    function currentClass(listClass) {
        return listClass.find(function (ele) { return colorsCard.includes(ele) })
    }

    function classeCard(count) {
        if (count === 1) {
            return { classCard: "bg-green", classCardText: "text-black" }
        } else if (count === 2) {
            return { classCard: "bg-rose", classCardText: "text-black" }
        } else if (count === 3) {
            return { classCard: "bg-orange", classCardText: "text-black" }
        } else if (count === 4) {
            return { classCard: "bg-blue", classCardText: "text-black" }
        } else if (count === 5) {
            return { classCard: "bg-blackGreen", classCardText: "text-white" }
        } else {
            return false
        }
    }

    useEffect(_ => {
        if (!intervalBingo) {
            clearInterval(intervalBingo)
            intervalBingo = setInterval(funcIntervalBingo, 1500)
        }
        return _ => clearInterval(intervalBingo)
    }, [])

    const countBallDranw = _ => {
        if (bingo) {
            let count = 0
            bingo.balls.forEach(element => {
                if (element.drawn) {
                    count++
                }
            });
            return count
        }

    }

    return (
        <>
            <div className="row m-1">
                <div className="col-2">
                    <Ball color={bingo ? bingo.color : ''} number_ball={bingo ? bingo.ball_drawn : ''} ></Ball>
                </div>


                <div className="col-6">
                    <table className="fs-4 " >
                        <tbody className="text-light  " >
                            <tr className="tabela ">
                                <td className="bg-warning text-center"> <span>B</span> </td>
                                {bingo ? bingo.balls.slice(0, 18).map((elem) => (<Col key={`ball${elem.number}`} number={elem.number} drawn={elem.drawn} ></Col>)) : ''}
                            </tr>

                            <tr>
                                <td className="bg-warning p-0 px-1 text-center"><span>I</span></td>
                                {bingo ? bingo.balls.slice(18, 36).map((elem) => (<Col key={`ball${elem.number}`} number={elem.number} drawn={elem.drawn} ></Col>)) : ''}
                            </tr>

                            <tr>
                                <td className="bg-warning p-0 px-1 text-center"><span>N</span></td>
                                {bingo ? bingo.balls.slice(36, 54).map((elem) => (<Col key={`ball${elem.number}`} number={elem.number} drawn={elem.drawn} ></Col>)) : ''}
                            </tr>

                            <tr>
                                <td className="bg-warning p-0 px-1 text-center"><span>G</span></td>
                                {bingo ? bingo.balls.slice(54, 72).map((elem) => (<Col key={`ball${elem.number}`} number={elem.number} drawn={elem.drawn} ></Col>)) : ''}
                            </tr>

                            <tr>
                                <td className="bg-warning p-0 px-1 text-center"><span>O</span></td>
                                {bingo ? bingo.balls.slice(72, 90).map((elem) => (<Col key={`ball${elem.number}`} number={elem.number} drawn={elem.drawn} ></Col>)) : ''}
                            </tr>
                        </tbody>
                    </table>
                    <p className="text-center m-1 mt-2"><span className="bg-primary text-light rounded-pill p-1 px-4 ">Bolas sorteadas: {bingo ? countBallDranw() : ''}</span></p>
                </div>
            </div>
            <ModalWin
                show={modalShow}
                onHide={() => setModalShow(false)}
                winners={winners}
            />
            <ModalTop 
                show={modalTopShow}
                onHide={() => setModalTopShow(false)}></ModalTop>
        </>
    );
}