import { createContext, useEffect, useState } from "react";

export const NextMatchContext = createContext({})

export const NextMatchProvider = props => {
    const [nextMatch, setNextMatch] = useState()
    
    useEffect(() => {
        const token = sessionStorage.getItem("token")

        fetch(`${window.urlAPI}/api/v1/nextMatch/`, {
            headers: {
                Authorization: `Token ${token}`
            }
        })
            .then(resp => {
                return resp.json()
            })
            .then(json => {
                setNextMatch({ ...json})
            })
            .catch(err => {
                console.log(`Erro aconteceu: ${err}`)
            })
    }, [])


    return (
        <NextMatchContext.Provider value={{nextMatch, setNextMatch}}>
            {props.children}
        </ NextMatchContext.Provider>
    );
};