import { useState } from "react"

export function CartelaVencedora(props) {
    const [linha1, setLinha1] = useState(props.balls.slice(0, 5))
    const [linha2, setLinha2] = useState(props.balls.slice(5, 10))
    const [linha3, setLinha3] = useState(props.balls.slice(10, 15))

    return (
        <>
            <table className="bg-info p-2 ms-2">
                <tbody >
                    <tr>
                        {formatLine(linha1).map(num => (
                            <td className={'fs-4 text-center'+`${num.number === "00" ? " bg-white text-white" : num.drawn ? " bg-warning text-white" : " bg-white"}`}>{num.number}</td>
                        ))}
                    </tr>
                    <tr>
                        {formatLine(linha2).map(num => (
                            <td className={'fs-4 text-center'+`${num.number === "00" ? " bg-white text-white" : num.drawn ? " bg-warning text-white" : " bg-white"}`}>{num.number}</td>
                        ))}
                    </tr>
                    <tr>
                        {formatLine(linha3).map(num => (
                            <td className={'fs-4 text-center '+`${num.number === "00" ? "   bg-white text-white" : num.drawn ? " bg-warning text-white" : " bg-white"}`}>{num.number}</td>
                        ))}
                    </tr>
                </tbody>
            </table>
        </>
    )
}

const formatLine = (nuns = []) => {
    let newNuns = []

    for (let i = 0; i < 9; i++) {
        newNuns.push({ number: "00", drawn: false })
    }
    for (let num of nuns) {
        if (num.number >= 1 & num.number <= 10) {
            newNuns[0] = num
        } else if (num.number >= 11 & num.number <= 20) {
            newNuns[1] = num
        } else if (num.number >= 21 & num.number <= 30) {
            newNuns[2] = num
        } else if (num.number >= 31 & num.number <= 40) {
            newNuns[3] = num
        } else if (num.number >= 41 & num.number <= 50) {
            newNuns[4] = num
        } else if (num.number >= 51 & num.number <= 60) {
            newNuns[5] = num
        } else if (num.number >= 61 & num.number <= 70) {
            newNuns[6] = num
        } else if (num.number >= 71 & num.number <= 80) {
            newNuns[7] = num
        } else if (num.number >= 81 & num.number <= 90) {
            newNuns[8] = num
        }
    }

    return newNuns
}