import { useContext } from 'react';
import 'keen-slider/keen-slider.min.css'
import { useKeenSlider } from 'keen-slider/react'
import './style.css'
import { Cartela } from './Cartela';
import { CardContext } from '../../providers/cards';



export function OwlCartela(props) {
    const cards = useContext(CardContext)
    const [sliderRef] = useKeenSlider({
        slides: {
            perView: 2.5,
            spacing: 1,
        },
    })

    const renderCards = () => {
        let cardtemp = []
        let colunasCards = []
        let countCards = []
        let start = 0
        let end = 3
        while (countCards.length < cards.cards.length) {
            cardtemp = cards.cards.slice(start, end)
            colunasCards.push(<ColCards key={`col${end}`} >
                {cardtemp.map(card => <Cartela key={`card${card.id}`} id={card.id} balls={card.balls} ></Cartela>)}
            </ColCards>)
            countCards = [...countCards, ...cards.cards.slice(start, end)]
            start += 3
            end += 3
        };
        return (
            <div ref={sliderRef} className="keen-slider">
                {colunasCards}
            </div>
        )
    }

    function ColCards(props) {
        return (
            <div className={'keen-slider__slide'}>
                {props.children}
            </div>
        )
    }

    return (
        <>
            {cards.cards ? renderCards() : undefined}
        </>
    )
}


export default OwlCartela  