import { useContext, useState } from "react";
import { NextMatchContext } from "../../providers/nextMatch";
import { Comprar } from "../Modal/Comprar";
import { Select } from "../Modal/Select";


export function Compra() {
    const [numPay, setNumPay] = useState(0)
    const nextMatchContext = useContext(NextMatchContext)
    const { nextMatch } = nextMatchContext ? nextMatchContext : {}
    function ativar() {
        document.getElementById('elemento',);

        document.getElementById('elemento').classList.toggle('comprar');

        document.getElementById('elemento2',);

        document.getElementById('elemento2').classList.toggle('cartelas2');
    }

    const buttonCard = op => {
        if (op & (numPay < nextMatch.number_cards_allowed)){
            setNumPay(numPay+1)
        }else if(op === false & (numPay > 0)){
            setNumPay(numPay - 1)
        }
    }

    return (
        <>
            <div >
                <div className="bg-white mt-1 rounded ">
                    <div className="text-center bg-info rounded-top py-0 ">
                        <h6>COMPRAR CARTELAS PARA A PRXIMA RODADA <button className="btn " onClick={ativar}><svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="white" className="bi bi-chevron-compact-up" viewBox="0 0 16 16">
                            <path fillRule="evenodd" d="M7.776 5.553a.5.5 0 0 1 .448 0l6 3a.5.5 0 1 1-.448.894L8 6.56 2.224 9.447a.5.5 0 1 1-.448-.894l6-3z" />
                        </svg>
                        </button></h6>
                    </div>
                    <div id="elemento" className="py-2  ">
                        <div className="container text-center ">
                            <div className="row">
                                <div className="col">
                                    <div className="text-light">
                                        <span className="bg-primary px-5 py-1"><span>{nextMatch ? nextMatch.num_next_cards ? nextMatch.num_next_cards : nextMatch.num_next_cards : ""}</span>/{nextMatch ? nextMatch.number_cards_allowed : ""}</span>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="text-light">
                                        <button onClick={_=>(buttonCard(false))} className="bg-primary  mx-1 border-0 " type="button" >-</button>
                                        <span id="cn" className="bg-primary px-4 px-2 py-0">{numPay}</span>
                                        <button onClick={_=>(buttonCard(true))} className="bg-primary mx-1 border-0">+</button>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="text-light">
                                        <span id="cnAll" className="bg-primary px-2 py-1"> Total {numPay ? (nextMatch ? numPay * nextMatch.unitary_value : 0).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' }) : ""}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="container text-center">
                            <div className="row">
                                <div className="col">
                                    <div>
                                        <h6 className="fs-4 mb-0">PREO CARTELA</h6>
                                        <span className="mt-0 bg-primary px-5 text-light">{nextMatch ? nextMatch.unitary_value.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' }) : ""}</span>
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="text-light mt-2">
                                        <Select min={1} max={nextMatch ? nextMatch.number_cards_allowed : 0} numPay={{numPay, setNumPay}} />
                                    </div>
                                </div>
                                <div className="col">
                                    <div className="text-light mt-2">
                                        <Comprar numPay={{setNumPay, numPay}} unitary_value={nextMatch ? nextMatch.unitary_value : 0} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}