import { Live } from "../Live/Index";
import { Cartelas } from "../Cartelas/Index";
import { Compra } from "../Cartelas/Compra";
import { RoundBalls } from "../RoundBalls";
import { NextRound } from "../RoundBalls/NextRound";
import { CardProvider } from "../../providers/cards";
import { BingoProvider } from "../../providers/bingo";
import { NextMatchProvider } from "../../providers/nextMatch";
import { StatusMatchCurrent } from "../StatusGame/matchCurrent";
import { StatusNextMatch } from "../StatusGame/nextMatch";
import { MatchContext, MatchProvider } from "../../providers/match";
import { InfoGame } from "../StatusGame/InfoGame";
import { PlayerCarousel } from "../StatusGame/PlayerCarousel";
import { useContext } from "react";


export function BodyMain() {
    const matchContext = useContext(MatchContext)
    const match = matchContext ? matchContext.match : {}
    const renderRound = _ => {
        if (match ? match.display_wait !== undefined : "") {

            return <>{match.display_wait ? <NextRound></NextRound> : <RoundBalls></RoundBalls>} </>
        }
    }

    return (
        <div className="container-fluid ">
            <div className="row mx-0 ">
                <div className="col-6 ">
                    <div className="row">
                        <NextMatchProvider>
                            <StatusNextMatch></StatusNextMatch>
                        </NextMatchProvider>
                        <StatusMatchCurrent></StatusMatchCurrent>
                        <Live />
                    </div>
                </div>
                <div className="col-6">
                    <div className="bg-info rounded ">
                        <InfoGame></InfoGame>
                        <PlayerCarousel></PlayerCarousel>
                    </div>
                    <div className="bg-info rounded mt-1">
                        <div className="col-12">
                            <div className="row">
                                <div className="row  ">
                                    <div className="col mt-1">
                                        <BingoProvider>
                                            {renderRound()}
                                        </BingoProvider>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col mt-1">
                        <CardProvider>
                            <Cartelas wait={match ? match.display_wait : true} />
                        </CardProvider>
                    </div>
                    <div className="compra mt-1" id="buyCard">
                        <NextMatchProvider>
                            <Compra/>
                        </NextMatchProvider>
                    </div>
                </div>
            </div>

        </div>
    );
}