import React, { useEffect, useState } from "react";
import "./style.css"
import { classesAwards } from "./classesAward";

export function Award(props) {
  let [classesCss, setClassesCss] = useState({})
  const [currentItem, setCurrentItem] = useState(0);

  useEffect(() => {
    setClassesCss(props ? classesAwards[props.award.name] : {})
  }, [])

  const clickControl = (e) => {
    const items = document.querySelectorAll(`.${classesCss.item}`);
    const maxItems = items.length;
    let curIt = currentItem

    let isLeft = e.target.classList.contains(classesCss.arrowLeft);

    if (isLeft && items.length > 0) {
      curIt -= 1;
    } else {
      curIt += 1;
    }

    if (curIt >= maxItems && items.length > 0) {
      curIt = 0;
    }

    if (curIt < 0 && items.length > 0) {
      curIt = maxItems - 1;
    }
    console.log(isLeft, curIt, items)
    items.forEach((item) => item.classList.remove(classesCss.currentItem));

    items[curIt].scrollIntoView({
      behavior: "smooth",
      inline: "center"
    });
    window.ia = items[curIt]

    items[curIt].classList.add(classesCss.currentItem);


    return curIt
  };

  function very(award) {

    if (award.winners.length >= 1) {
      let names = award.winners.map((w, i) => {
        return <>
          <div className={i === 0 ? `${classesCss.item} ${classesCss.currentItem}` : `${classesCss.item}`}>
            <h5 className="fontes2 fs-4">{w.user.name}</h5>
          </div>
        </>
      })
      return names
    } else if (award.winners.length === 0) {
      return <>
        <div className={`${classesCss.item} ${classesCss.currentItem}`}>
          <h5 className="fontes2 fs-4">{award.name}</h5>
        </div>
      </>
    }
  }

  return (
    <>
      <div className="bg-primary text-center rounded fs-6 text-white">

        <button className={`${classesCss.arrowLeft} ${classesCss.control}`} aria-label="Previous image" onClick={_ => setCurrentItem(clickControl(_))}></button>
        <button className={`${classesCss.arrowRight} ${classesCss.control}`} aria-label="Next Image" onClick={_ => setCurrentItem(clickControl(_))}></button>
        <div className={`${classesCss.galleryWrapper}`}>
          <div className={`${classesCss.gallery}`}>
            {props ? very(props.award) : ''}
          </div>
        </div>
        <div>

          <span> {props ? props.award.value.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' }) : props} {props ? props.award.winners.length > 0 ? <><span className="fs-4 pt-0" >- {props.award.name}</span> <br></br> </> : '' : ''}</span>
        </div>
      </div>
    </>
  );
}



