import { Button, Modal } from 'react-bootstrap';
import { useContext, useEffect, useState } from 'react';


function MyVerticallyCenteredModal(props) {

  async function purchase() {
    const token = sessionStorage.getItem("token")

    props.onHide()
    await fetch(`${window.urlAPI}/api/v1/cards/`, {
      method: 'POST',
      body: JSON.stringify({
        number_cards: parseInt(props.numPay)
      }),
      headers: {
        'Authorization': `Token ${token}`,
        'Content-Type': 'application/json'
      }
    })
      .then(resp => {
        if (resp.ok){
          let elem = document.querySelector("#root > div:nth-child(1) > div.container.menu > div > div > div > div:nth-child(1) > div > div:nth-child(1) > div > div:nth-child(2) > div > div.col > span > span")
          elem.textContent = `${parseInt(elem.textContent)+props.numPay}`
          elem = document.querySelector("#buyCard > div:nth-child(1) > div:nth-child(1) > div:nth-child(2) > div:nth-child(1) > div:nth-child(1) > div:nth-child(1) > div:nth-child(1) > span:nth-child(1) > span:nth-child(1)")
          elem.textContent = `${parseInt(elem.textContent)+props.numPay}`
        }
        return resp.json()
      })
      .then(json => {
        console.log(json)
      })
      .catch(err => {
        console.log(`Erro aconteceu: ${err}`)
      })

    const ret = await fetch(`${window.urlAPI}/api/v1/auth/Me`, {
      headers: {
        Authorization: `Token ${sessionStorage.getItem("token")}`
      }
    })
      .then(resp => {
        return resp.json()
      })
      .then(json => {
        console.log(json)
        let elemMoney = document.querySelector("p.mx-2:nth-child(2) > span:nth-child(2)")
        let elemBonus = document.querySelector("p.mx-2:nth-child(3) > span:nth-child(2)")
        elemMoney.textContent = parseFloat(json.money).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })
        elemBonus.textContent = parseFloat(json.bonus).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })
        return { ...json }
      })
      .catch(err => {
        console.log(`Erro aconteceu: ${err}`)
      })
    return ret
  }

  return (
    <Modal
      {...props}
      size="md"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Body className='bg-primary rounded px-5'>
        <h4 className='text-center '>CONFIRMAR COMPRA</h4>
        <h6 className='text-center my-4'>
          Sua compra {props.numPay} cartelas por {props.numPay ? (props.numPay * props.unitary_value).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' }) : props.numPay}
        </h6>
        <div className="container">
          <div className="row">
            <div className="col">
            </div >
            <div className="col ">
              <Button className='rounded-pill bg-danger text-black mb-0' onClick={props.onHide}>CANCELAR</Button>
            </div>
            <div className="col">
              <Button className='rounded-pill bg-success text-light' onClick={async _ => await purchase()}>CONFIRMAR</Button>
            </div>
            <div className="col">
            </div>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
}


export function Comprar(props) {

  const [modalShow, setModalShow] = useState(false);

  return (
    <>
      <button className="bg-warning border-0  fs-6 rounded-pill text-light " onClick={() => setModalShow(true)} type="button" ><span className="p-1">Comprar</span></button>
      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        unitary_value={props.unitary_value}
        numPay={props.numPay.numPay}
        setNumPay={props.numPay.setNumPay}
      />


    </>
  );







}



