import { useEffect, useState } from "react";
import { DateTime } from "luxon";
DateTime.local({ zone: "pt-br" })

export function NextRound() {

    
    const [standby, setStandby] = useState(null)
    const [syncc, setSyncc] = useState(undefined)
    const [sync, setSync] = useState(undefined)
    const [mTime, setmTime] = useState(null)
    let intervalTime
    let intervalSync

    const funcInterval =  s => {
        const token = sessionStorage.getItem("token")

        fetch(`${window.urlAPI}/api/v1/match_sync/`, {
            headers: {
                Authorization: `Token ${token}`
            }
        })
            .then(resp => {
                return resp.json()
            })
            .then(json => {
                setSyncc({...json})
                if (json.started) {
                    window.location.reload();
                };
                if (localStorage.getItem('syncc')){
                    const ss = JSON.parse(localStorage.getItem("syncc"))
                    if(json.start_datetime !== ss.start_datetime){
                        localStorage.setItem("syncc", JSON.stringify(json))
                        setSync({...json})
                    }
                    if (json.standby !== ss.standby){
                        setStandby(json.standby)
                        localStorage.setItem("syncc", JSON.stringify(json))
                    }

                }else{
                    localStorage.setItem("syncc", JSON.stringify(json))
                    setSync({...json})
                    setStandby(json.standby)
                }
            })
            .catch(err => {
                console.log(`Erro aconteceu: ${err}`)
            });
        
    }

    const updateSync = _ => {
        intervalSync = !intervalSync && setInterval(funcInterval , 3000)
    }

    const updateTime = _ => {
        if (!intervalTime) {
            if (sync) {
                intervalTime = !intervalTime && setInterval(() => {
                    let m = DateTime.fromISO(sync.start_datetime)
                    let mDiff = m.diffNow(['hours', 'minutes', 'seconds'])
                    setmTime(mDiff)
                    if (mDiff.hours === 0 && mDiff.minutes === 0 && mDiff.seconds === 0){
                        clearInterval(intervalTime)
                        window.location.reload()
                    }
                }, 1000)
            }
        } 

    }

    useEffect(() => {
        updateSync()

        return _=>clearInterval(intervalSync)
    }, [])

    useEffect(_ => {
        updateTime()
        return _=> clearInterval(intervalTime)
    }, [sync])

    const buyCard = s => {
        if (s){
            const elem = document.querySelector("#buyCard")
            elem.classList.contains("d-none") ? elem.classList.remove("d-none") : console.log()
        }
        else{
            const elem = document.querySelector("#buyCard")
            elem.classList.contains("d-none") ? console.log() : elem.classList.add("d-none")
        }
    }

    return (
        <div>
            <div className="row">
                <div className="col ">
                    <div className="row box">
                        <div className="col ">
                            <div className="p-5">
                                <h6 className="text-center text-info ">PRXIMA RODADA!<br />
                                </h6>
                                <h6 className="bg-primary rounded px-3 text-center text-light cronometro">{mTime ? `${mTime ? mTime.hours : ''}:${mTime ? mTime.minutes : ''}:${mTime ? parseInt(mTime.seconds) : ''}`: ''}</h6>
                            </div>
                        </div>

                        <div className="col">
                            <div className="py-5 text-info ">
                                <div className=" border rounded border-primary mx-2">
                                    <h6 className="text-center fw-normal">VENDA DE CARTELAS</h6>
                                    {standby !== null ? standby ? <><h5 className="text-center">DISPONIVEIS</h5></> : <><h5 className="text-center">NO DISPONIVEL</h5></> : ''}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>{standby ? buyCard(standby) : buyCard(standby)}
            {/* <ModalWin></ModalWin> */}
        </div>  
    );
}