import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { Award } from '.';
import { useState } from 'react';

export function Owldemo1(props) {
  const [responsive, setResponsive] = useState({
    0: {
      items: 1,
    },
    450: {
      items: 2,
    },
    600: {
      items: 3,
    },
    1000: {
      items: 3,
    },
  })

  function CarroselAwards(){
    let awards = []
  
    props.awards.forEach(a => (
      awards.push(<Award award={a} key={a.name} >
      </Award>)
    ))
  
    return (<OwlCarousel
      items={3}
      className="owl-theme "
      responsive={responsive}
      center
      startPosition="1"
      margin={8}
    >
      {awards.map(_ => (_))}
    </OwlCarousel>)
  }

  return (
    <>
      <div>
        <div className='container-fluid' >
          <div className="card1" >
          </div>
        </div>
        <div className='m-2'>
  
          {props.awards ? CarroselAwards() : undefined}
  
        </div>
      </div>
    </>
  )
}









export default Owldemo1  